*{
  font-family:   Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
  color: #666;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

input{
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card {
  align-content: center;  
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: 30px;
  margin-right: 10px;
  box-shadow: 1px 1px 4px rgba(134, 133, 133, 0.5),  1px 1px 4px rgba(134, 133, 133, 0.5);
  width: fit-content;
}

.card:hover{  
  box-shadow: 3px 3px 8px rgba(134, 133, 133, 0.5),  1px 1px 8px rgba(134, 133, 133, 0.5);
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img {
  display: block;
  margin: auto;
  max-height: 170px;
}